<template>
  <div class="hello">
    <el-row :gutter="20">
        <el-col :span="2">
            <el-button @click="goBack()" type="primary">返回上一级</el-button>
        </el-col>
    </el-row><br>
    <el-row :gutter="20" class="bgep">
        <br>
        <el-col :span="8" :offset="4">
            <div>
                <el-form :model="roleForm" status-icon :rules="rules" ref="roleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="角色编码" prop="roleCode">
                        <el-input v-model="roleForm.roleCode"></el-input>
                    </el-form-item>
                    <el-form-item label="角色名称" prop="roleName">
                        <el-input v-model="roleForm.roleName"></el-input>
                    </el-form-item>
                   
                    <el-form-item label="生效日期">
                        <el-col :span="24">
                            <el-date-picker type="date" placeholder="选择日期" value-format="yyyyMMddHHmmss" v-model="roleForm.startTime" style="width: 100%;"></el-date-picker>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="失效日期">
                        <el-col :span="24">
                            <el-date-picker type="date" placeholder="选择日期" value-format="yyyyMMddHHmmss" v-model="roleForm.overTime" style="width: 100%;"></el-date-picker>
                        </el-col>
                    </el-form-item>

                    <el-form-item label="状态" prop="roleStatus">
                        <el-radio-group v-model="roleForm.roleStatus">
                            <el-radio label="1">有效</el-radio>
                            <el-radio label="2">无效</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="备注" prop="userRemarks">
                        <el-input type="textarea" v-model="roleForm.roleRemarks"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="submitForm('roleForm')">立即创建</el-button>
                        <el-button @click="resetForm('roleForm')">重置</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
 data() {
      return {
        roleForm: {
          id:'',
          roleCode: '',
          roleName: '',
          startTime: '',
          overTime:'',
          roleStatus: '',
          roleRemarks: '',
        },
        rules: {
          roleCode: [
            { required: true, message: '请输入角色编码', trigger: 'blur' },
          ],
          roleName: [
            { required: true, message: '请输入角色名称', trigger: 'blur' },
            { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
          ],
          roleStatus: [
            { required: true, message: '请选择状态', trigger: 'change' }
          ],
          roleRemarks: [
            { required: false, message: '', trigger: 'blur' }
          ],
        }
      };
    },
    mounted(){
        var storage = localStorage;
        var role =JSON.parse(storage.getItem('updateRole'));
        role.roleStatus = role.roleStatus + '';
        if(role.roleRemarks == null){
            role.roleRemarks = '';
        }
        this.roleForm = role;
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.roleForm);
            var that = this;
            var url="/updateRole";
            let param = new URLSearchParams();
            param.set("id",that.roleForm.id);
            param.set("roleCode",that.roleForm.roleCode);
            param.set("roleName",that.roleForm.roleName);
            param.set("startTime",that.roleForm.startTime);
            param.set("overTime",that.roleForm.overTime);
            param.set("roleStatus",that.roleForm.roleStatus);
            param.set("roleRemarks",that.roleForm.roleRemarks);
            that.$axios
              .post(url,param)
              .then(function(resp) {
               if(resp.data.success){
                 alert('修改成功!!');
                that.$router.push('/home/role');
               }
            })
              .catch(function (error) { // 请求失败处理
                  console.log(error);
            });
          } else {
            alert('修改失败!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      goBack(){
        var that = this;
        that.$router.push('/home/role');
      },
    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .bgep{
        background-color: white;
        color:black;
    }
</style>
