<template>
    <div>
        <div>
            <h2>用户权限管理系统</h2><br>
            <el-row :gutter="20">
                <el-col :span="6" :offset="9">
                    <div>
                        <el-input v-model="username" placeholder="请输入用户名"></el-input><br><br>
                        <el-input placeholder="请输入密码" v-model="password" show-password></el-input><br><br>
                        <el-button v-on:click="login" type="primary" round>登录</el-button>
                        <br><span>（测试用户名:admin，密码:123）</span>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
export default {
  data(){
      return {
          username: '',
          password:'',
      }
  },

   methods:{
      // 方法定义
      login(){
          var that = this;
          var url="/login/"+this.username+"/"+this.password;
          that.$axios
            .get(url)
            .then(function(resp) {
            if(resp.data.code==200){
                //跳转
                var d=JSON.stringify(resp.data.data);//json转string
                var storage = localStorage;
                storage.setItem('user',d);
                that.$router.push("/home");
            }else{
                alert("用户名/密码错误或账户失效!!!");
            }
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
            });
      },
     
    }
}
</script>

