<template>
  <div class="hello">
    <el-row :gutter="20">
        <el-col :span="2">
            <el-button @click="goBack()" type="primary">返回上一级</el-button>
        </el-col>
    </el-row><br>
    <el-row :gutter="20" class="bgep">
        <br>
        <el-col :span="8" :offset="4">
            <div>
                <el-form :model="userForm" status-icon :rules="rules" ref="userForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="用户名" prop="username">
                        <el-input v-model="userForm.username"></el-input>
                    </el-form-item>
                    <el-form-item label="姓名" prop="name">
                        <el-input v-model="userForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model="userForm.password" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="checkPassword">
                        <el-input type="password" v-model="userForm.checkPassword" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="性别" prop="sex">
                        <el-select v-model="userForm.sex" placeholder="请选择性别">
                            <el-option label="男" value="男"></el-option>
                            <el-option label="女" value="女"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="联系电话" prop="userPhone">
                        <el-input v-model="userForm.userPhone" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱" prop="email">
                        <el-input v-model="userForm.email" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="状态" prop="userStatus">
                        <el-radio-group v-model="userForm.userStatus">
                            <el-radio label="1">有效</el-radio>
                            <el-radio label="2">无效</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="备注" prop="userRemarks">
                        <el-input type="textarea" v-model="userForm.userRemarks"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="submitForm('userForm')">立即创建</el-button>
                        <el-button @click="resetForm('userForm')">重置</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
 data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.userForm.checkPassword !== '') {
            this.$refs.userForm.validateField('checkPassword');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.userForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
       var checkPhone = (rule, value, callback) => {
        if (value) {
          var partten = /^1[3,4,5,7,8]\d{9}$/;
          if(!partten.test(value)){
            callback(new Error('手机号码格式错误'));
          }
          callback();
        };
        
      };
      return {
        userForm: {
          username: '',
          name: '',
          password: '',
          checkPassword:'',
          sex: '',
          userPhone: '',
          email: '',
          userStatus: '',
          userRemarks: ''
        },
        rules: {
          password: [
            { validator: validatePass, trigger: 'blur' },
            { required: true, message: '请输入密码', trigger: 'blur' },
          ],
          checkPassword: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          userPhone:[
            { validator: checkPhone, trigger: 'blur' }  
          ],
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '请选择性别', trigger: 'change' }
          ],
          userStatus: [
            { required: true, message: '请选择状态', trigger: 'change' }
          ],
          userRemarks: [
            { required: false, message: '', trigger: 'blur' }
          ],
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.userForm);
            var that = this;
            var url="/insertUser";
            let param = new URLSearchParams();
            param.set("username",that.userForm.username);
            param.set("name",that.userForm.name);
            param.set("password",that.userForm.password);
            param.set("sex",that.userForm.sex);
            param.set("userPhone",that.userForm.userPhone);
            param.set("email",that.userForm.email);
            param.set("userStatus",that.userForm.userStatus);
            param.set("userRemarks",that.userForm.userRemarks);

            that.$axios
              .post(url,param)
              .then(function(resp) {
               if(resp.data.success){
                 alert('提交成功!!');
                that.$router.push('/home/user');
               }
            })
              .catch(function (error) { // 请求失败处理
                  console.log(error);
            });
          } else {
            alert('提交失败!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      goBack(){
        var that = this;
        that.$router.push('/home/user');
      },
    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .bgep{
        background-color: white;
        color:black;
    }
</style>
