<template>
  <div>
    <div>
       <div align="left"><h2 style="color:	Gray">角色管理</h2></div>
       <el-row :gutter="20">
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-input
            size="medium"
            placeholder="请输入角色名称"
            suffix-icon="el-icon-edit el-input__icon"
            v-model="roleName">
          </el-input>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-select size="medium" v-model="roleStatus" placeholder="请选择状态">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
       
        <el-col :span="6">
          <el-button
          v-if="btcheck.searchButton"
          type="primary"
          @click="search()">查询</el-button>
          <el-button
          v-if="btcheck.addButton"
          @click="addRole()">新增</el-button>
          <el-button
          type="danger"
          v-if="btcheck.delButton"
          @click="deleteMore()">删除</el-button>
        </el-col>
        <el-col :span="10">
          
        </el-col> 
      </el-row>
    </div><br>
    <el-table
      ref="multipleTable"
      :data="tableData"
      stripe
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      
      <el-table-column
        prop="id"
        label="序号">
      </el-table-column>
      <el-table-column
        prop="roleCode"
        label="角色编码"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="roleName"
        label="角色名称"
        show-overflow-tooltip>
      </el-table-column>
    
      <el-table-column
        label="生效日期"
        width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ timeFormat(scope.row.startTime) }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="失效日期"
        width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ timeFormat(scope.row.overTime) }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="状态"
        width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.roleStatus===1?'有效':'无效' }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="300" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            v-if="btcheck.updateButton"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            v-if="btcheck.delButton"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          <el-button
            size="mini"
            v-if="btcheck.setRolePowerButton"
            @click="handleAddRolePermissions(scope.$index, scope.row)">设置角色权限</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">
      <br>
      <el-pagination
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        background
        layout="total, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
     <!--删除的弹窗 -->
    <el-dialog
      title="提示"
      :visible.sync="delDialog"
      width="30%"
      center>
      <span>是否删除序号{{delData}}的数据？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delDialog = false">取 消</el-button>
        <el-button type="primary" @click="deleteRole()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
 data(){
   return{
     tableData:[],
     multipleSelection: [],
     total:0,
     pageSize:10,//分页数目
     roleName:'',
     roleStatus:'',
     options: [
        {
          value: '',
          label: '请选择状态'
        },
        {
          value: '1',
          label: '有效'
        }, {
          value: '2',
          label: '无效'
        }],
     btcheck:{
       //权限列
       searchButton:false,//查询
       addButton:false,//新增
       delButton:false,//删除
       updateButton:false,//编辑
       setRolePowerButton:false,//设置角色
     },
     delDialog:false,//删除弹窗
     delData:[],//需删除的对象
   }
 },
  mounted(){
         this.searchdata(1);
         this.updatePowers();
  },
   methods: {
      //删除弹出
      handleDelete(index, row) {
        this.delData=[];
        this.delDialog = true;
        this.delData.push(row.id);
      },
      //批量删除
      deleteMore(){
        this.delDialog = true;
        this.delData=[];
        console.log(this.multipleSelection);
        for(var j=0 ,len=this.multipleSelection.length;j<len;j++){
           this.delData.push(this.multipleSelection[j].id);
        }
         console.log("多选:"+this.delData);
      },
      //删除
      deleteRole(){
        var that = this;
        var data = that.delData;
          var url="/deleteRole";
          let param = new URLSearchParams();
          param.append("rid",that.delData);
          that.$axios
            .post(url,param)
            .then(function(resp) {
             if(resp.data.success){
               that.delDialog = false;
               that.searchdata(1);
             }
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
          });
      },
    //跳转添加用户
    addRole(){
       var that = this;
      that.$router.push('/home/addRole');
    },
     //更新权限
    updatePowers(){
      var that = this;
      var storage = localStorage;
      var powers = JSON.parse(storage.getItem('powers'));
      console.log(powers);
      for(var j=0,len=powers.length;j<len;j++){
        if(powers[j].id == 9){
          that.btcheck.searchButton = true;
        }
        if(powers[j].id == 10){
          that.btcheck.addButton = true;
        }
        if(powers[j].id == 11){
          that.btcheck.delButton = true;
        }
        if(powers[j].id == 12){
          that.btcheck.updateButton = true;
        }
        if(powers[j].id == 19){
          that.btcheck.setRolePowerButton = true;
        }
      }
      console.log(powers);
    },
    //查询数据
    search(){
      var that = this;
      this.searchdata(1);
    },
    //分页
    searchdata(pageNumber){
         var that = this;
          var url="/queryRolePage";
          let param = new URLSearchParams();
          param.append("pageSize",that.pageSize);
          param.append("pageNumber",pageNumber);
          param.append("roleName",that.roleName);
          param.append("roleStatus",that.roleStatus);
          that.$axios
            .post(url,param)
            .then(function(resp) {
              that.tableData = resp.data.data;
              that.total = resp.data.total;
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
          });
    },
      //多选
      handleSelectionChange(val) {
        this.multipleSelection = val;
        
      },
      //编辑
      handleEdit(index, row) {
        console.log(index, row);
        var that = this;
        var storage = localStorage;
        storage.setItem('updateRole',JSON.stringify(row));
        that.$router.push('/home/updateRole');
      },
      //设置角色权限
      handleAddRolePermissions(index, row) {
        var that = this;
        console.log(index, row);
        that.$router.push({
                name:'rolePermissions',
                params:{
                    role:row
                }
            });
      },
     //跳转页数
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.searchdata(val);
      },
    //时间格式转换
      timeFormat (time){
           if (!time) {
              return '';
          }
          return time.replace(/(\d{4})(\d{2})(\d{2})(\d{2})(\d{2})(\d{2})/g, "$1-$2-$3 $4:$5:$6");
      },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
