<template>
  <div class="home">
    <el-container>
      <el-aside width="200px">
         <el-row class="tac">
          <el-col :span="24">
            <h5>用户权限管理系统</h5>
            <el-menu
              default-active="1"
              class="el-menu-vertical-demo">
              <el-menu-item index="1" v-on:click="toHello">
                <i class="el-icon-location"></i>
                <span slot="title">首页</span>
              </el-menu-item>
              <el-submenu index="2">
                  <template slot="title">
                    <i class="el-icon-menu"></i>
                    <span>系统管理</span>
                  </template>
                  <el-menu-item-group>
                    <template v-for="(val,key) in powers">
                      <el-menu-item v-bind:index="2-val.id.toString()"  v-if="val.menuContents == 2 " :key="key"  v-on:click="toPage(val.menuUrl)">
                        <i v-bind:class="val.menuPhoto"></i>{{val.menuName}}
                      </el-menu-item>
                    </template>
                  </el-menu-item-group>
              </el-submenu>
            </el-menu>
          </el-col>
        </el-row>
      </el-aside>
      <el-container>
        <el-header>
          <span>{{user.username}},欢迎您，点击这里</span>
           <el-link class="fontsize" type="primary" :underline="false" v-on:click="toLogin">退出</el-link>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>

  </div>
</template>

<script>
export default {
  name: 'home',
  data(){
    return{
        user:{},
        powers:[],
    }
  },
  mounted(){
    var storage = localStorage;
    this.user = JSON.parse(storage.getItem('user'));//string转json
    this.$router.push('/home/hello');
    this.menulist();
  },
  methods: {
      //菜单列表
      menulist(){
        let that = this;
        that.queryHasMenu();
      },
      //已有权限
      queryHasMenu(){
          var that = this;
          var url="/queryHasMenu/"+that.user.roleId;
          that.$axios
              .get(url)
              .then(function(resp) {
              if(resp.data.success){
                  that.powers = resp.data.data;
                  console.log("powers:"+that.powers);
                  var storage = localStorage;
                  storage.setItem('powers',JSON.stringify(that.powers))
              }
            })
              .catch(function (error) { // 请求失败处理
                  console.log(error);
              });
      },
      //跳转页面
      toPage(url){
        var that = this;
        console.log(url);
        that.$router.push(url);
      },
      //首页
      toHello(){
        var that = this;
        that.$router.push('/home/hello');
      },
      //退出至登录
      toLogin(){
        var that = this;
        that.$router.push('/login');
      },
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: right;
    line-height: 60px;
    font-size: 18px;
  }
  .fontsize{
     font-size: 18px;
  }
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
  }
  
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    /* line-height: 160px; */
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  html,body,#app,.home,.el-container{
        /*设置内部填充为0，几个布局元素之间没有间距*/
        padding: 0px;
         /*外部间距也是如此设置*/
        margin: 0px;
        /*统一设置高度为100%*/
        height: 100%;
    }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  #app{
    margin-top: 0px;
  }
</style>
