<template>
  <div>
    <div>
       <div align="left"><h2 style="color:	Gray">菜单管理</h2></div>
       <el-row >
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-input
            size="medium"
            placeholder="请输入菜单名称"
            suffix-icon="el-icon-edit el-input__icon"
            v-model="menuName">
          </el-input>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-select size="medium" v-model="menuStatus" placeholder="请选择状态">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-select size="medium" v-model="menuContents" placeholder="请选择菜单类型">
            <el-option
              v-for="item in menutype"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="6">
          <el-button
          type="primary"
          @click="search()" v-if="btcheck.searchButton">查询</el-button>
          <el-button
          v-if="btcheck.addButton"
          @click="addMenu()">新增</el-button>
          <el-button
          type="danger"
          v-if="btcheck.delButton"
          @click="deleteMore()">删除</el-button>
        </el-col> 
      </el-row>
    </div><br>
    <el-table
      ref="multipleTable"
      :data="tableData"
      stripe
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      
      <el-table-column
        prop="id"
        label="序号"
         width="100">
      </el-table-column>
      <el-table-column
        prop="menuName"
        label="菜单名称"
        width="100">
      </el-table-column>

      <el-table-column
        label="上级菜单"
        width="100">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{{1:'系统管理',2: '用户管理', 3:'角色管理',4:'菜单管理'}[scope.row.parentMenu]}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="菜单类型"
        width="100">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{{1: '目录', 2:'菜单',3:'功能'}[scope.row.menuContents]}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="menuIdentification"
        label="菜单标识"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="menuUrl"
        label="菜单地址"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="menuPhoto"
        label="图标"
        show-overflow-tooltip>
      </el-table-column>
       <el-table-column
        prop="sortNum"
        label="排序号"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        label="状态"
        width="100">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.menuStatus===1?'有效':'无效' }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="300" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            v-if="btcheck.updateButton"
            @click="handleEdit(scope.$index, scope.row)">修改</el-button>
          <el-button
            size="mini"
            type="danger"
            v-if="btcheck.delButton"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">
      <br>
      <el-pagination
        @current-change="handleCurrentChange"
        :page-size="pageSize"
        background
        layout="total, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <!--删除的弹窗 -->
    <el-dialog
      title="提示"
      :visible.sync="delDialog"
      width="30%"
      center>
      <span>是否删除序号{{delData}}的数据？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delDialog = false">取 消</el-button>
        <el-button type="primary" @click="deleteMenu()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
 data(){
   return{
     tableData:[],
     multipleSelection: [],
     total:0,
     pageSize:10,//分页数目
     menuName:'',
     menuStatus:'',
     menuContents:'',
     options: [
        {
          value: '',
          label: '请选择状态'
        },
        {
          value: '1',
          label: '有效'
        }, {
          value: '2',
          label: '无效'
        }],
    menutype: [
        {
          value: '',
          label: '请选择菜单类型'
        },
        {
          value: '1',
          label: '目录'
        }, {
          value: '2',
          label: '菜单'
        }, {
          value: '3',
          label: '功能'
        }],
    btcheck:{
       //权限列
       searchButton:false,//查询
       addButton:false,//新增
       delButton:false,//删除
       updateButton:false,//编辑
     },
    delDialog:false,//删除弹窗
    delData:[],//需删除的对象
   }
 },
  mounted(){
         this.searchdata(1);
         this.updatePowers();
  },
   methods: {
     //删除弹出
      handleDelete(index, row) {
        this.delData=[];
        this.delDialog = true;
        this.delData.push(row.id);
      },
      //批量删除
      deleteMore(){
        this.delDialog = true;
        this.delData=[];
        console.log(this.multipleSelection);
        for(var j=0 ,len=this.multipleSelection.length;j<len;j++){
           this.delData.push(this.multipleSelection[j].id);
        }
         console.log("多选:"+this.delData);
      },
      //删除
      deleteMenu(){
        var that = this;
        var data = that.delData;
          var url="/deleteMenu";
          let param = new URLSearchParams();
          param.append("mid",that.delData);
          that.$axios
            .post(url,param)
            .then(function(resp) {
             if(resp.data.success){
               that.delDialog = false;
               that.searchdata(1);
             }
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
          });
      },
    //添加菜单页面
    addMenu(){
      var that = this;
      that.$router.push('/home/addMenu');
    },
    //更新权限
    updatePowers(){
      var that = this;
      var storage = localStorage;
      var powers = JSON.parse(storage.getItem('powers'));
      console.log(powers);
      for(var j=0,len=powers.length;j<len;j++){
        if(powers[j].id == 13){
          that.btcheck.searchButton = true;
        }
        if(powers[j].id == 14){
          that.btcheck.addButton = true;
        }
        if(powers[j].id == 15){
          that.btcheck.delButton = true;
        }
        if(powers[j].id == 16){
          that.btcheck.updateButton = true;
        }
      }
      console.log(powers);
    },
    //查询数据
    search(){
      var that = this;
      this.searchdata(1);
    },
    //分页
    searchdata(pageNumber){
         var that = this;
          var url="/queryMenuPage";
          let param = new URLSearchParams();
          param.append("pageSize",that.pageSize);
          param.append("pageNumber",pageNumber);
          param.append("menuName",that.menuName);
          param.append("menuStatus",that.menuStatus);
          param.append("menuContents",that.menuContents);
          that.$axios
            .post(url,param)
            .then(function(resp) {
              that.tableData = resp.data.data;
              that.total = resp.data.total;
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
          });
    },
      //多选
      handleSelectionChange(val) {
        this.multipleSelection = val;
        
      },
      //编辑
      handleEdit(index, row) {
        console.log(index, row);
        var that = this;
        var storage = localStorage;
        storage.setItem('updateMenu',JSON.stringify(row));
        that.$router.push('/home/updateMenu');
      },
     //跳转页数
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.searchdata(val);
      },
    //时间格式转换
      timeFormat (time){
           if (!time) {
              return '';
          }
          return time.replace(/(\d{4})(\d{2})(\d{2})(\d{2})(\d{2})(\d{2})/g, "$1-$2-$3 $4:$5:$6");
      },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
