<template>
    <div>
    <el-row :gutter="20">
        <el-col :span="2">
            <el-button @click="goBack()" type="primary">返回上一级</el-button>
        </el-col>
    </el-row><br>
        <div>
            <el-row :gutter="20">
                <el-col :span="8" :offset="6">
                    <el-row :gutter="20">
                        <el-col :span="8"><span class="ff">*角色名称:</span></el-col>
                        <el-col :span="16">
                            <el-input
                                placeholder="请输入内容"
                                v-model="role.roleName">
                            </el-input>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row><br>
            <el-row :gutter="20">
                <el-col :span="8" :offset="6">
                    <el-row :gutter="20">
                        <el-col :span="8"><span class="ff">*设置权限名称:</span></el-col>
                        <el-col :span="16">
                            <el-tree
                                :data="data"
                                show-checkbox
                                default-expand-all
                                :default-checked-keys="powers"
                                node-key="id"
                                ref="tree"
                                highlight-current
                                :props="defaultProps">
                            </el-tree>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
        </div>
        <br>
        <div class="buttons">
            <el-button @click="getCheckedKeys">提交设置</el-button>
            <el-button @click="resetChecked">清空</el-button>
        </div>
    </div>
</template>

<script>
export default {
  data(){
    return{
        role:{},
        data: [],
        powers:[],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        usercheck:false,
        rolecheck:false,
        menucheck:false,
    }
  },
  mounted(){
      // 实例已经创建完成之后被调用。在这一步，实例已完成以下的配置：数据观测(data observer)，属性和方法的运算， watch/event 事件回调。然而，挂载阶段还没开始，$el 属性目前不可见。
    this.role =  this.$route.params.role;
    //权限列表
    this.queryRolePermissionsList();
    //已有权限
    this.queryHasRolePermissions();
  },
  methods: {
       //已有权限
    queryHasRolePermissions(){
        var that = this;
        var url="/queryRolePowers/"+that.role.id;
        that.$axios
            .get(url)
            .then(function(resp) {
            if(resp.data.success){
                let list = resp.data.data;
                console.log(list);
                for(let j = 0,len=list.length; j < len; j++) {
                    if(list[j] == 1 || list[j] == 2 || list[j] == 3 || list[j] == 4){
                        delete list[j];
                    };
                };
                console.log("list:"+list);
                that.powers =list;
            }
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
            });
    },
      //权限列表
     queryRolePermissionsList(){
        var that = this;
        var url="/queryMenuTree";
        that.$axios
            .get(url)
            .then(function(resp) {
            if(resp.data.success){
                that.data = resp.data.data;
            }
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
            });
     },
    //通过key获取menuid,提交
      getCheckedKeys() {
          let that = this;
          let url="/updateRolePowers";
          let param = new URLSearchParams();
          let menuList = that.$refs.tree.getCheckedKeys();
          console.log(menuList);
          if(menuList!=undefined){
               menuList.push(1);
          }
          menuList.forEach((item,i) => {
                if(item===5||item ===6||item ===7||item ===8||item ===17||item ===18){
                    that.usercheck=true;
                }
                if(item===9||item ===10||item ===11||item ===12||item ===19){
                    that.rolecheck=true;
                }
                if(item===13||item ===14||item ===15||item ===16){
                    that.menucheck=true;
                }
            })
          if(that.usercheck){
              menuList.push(2);
          };
          if(that.rolecheck){
              menuList.push(3);
          };
          if(that.menucheck){
              menuList.push(4);
          };
          menuList = that.unique4(menuList);
          console.log(menuList);
          param.append("menuIdList",menuList);
          param.append("roleId",that.role.id);
          that.$axios
            .post(url,param)
            .then(function(resp) {
             if(resp.data.success){
                 that.queryHasRolePermissions();
                 alert("提交成功!请刷新页面!!!");
             }else{
                 alert("请求处理失败!!!!");
             }
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
          });
      },
      resetChecked() {
        this.$refs.tree.setCheckedKeys([]);
      },
      //返回角色页面
      goBack() {
        var that = this;
        that.$router.push('/home/role');
      },
      //去除重复数组
      unique4(arr){
        var hash=[];
        for (var i = 0; i < arr.length; i++) {
            for (var j = i+1; j < arr.length; j++) {
            if(arr[i]===arr[j]){
                ++i;
            }
            }
            hash.push(arr[i]);
        }
        return hash;
     }
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .bgep{
        background-color: white;
        color:black;
    }
    .ff{
        font-size: 20px;
    }
</style>
