<template>
  <div class="hello">
    <el-row :gutter="20">
        <el-col :span="2">
            <el-button @click="goBack()" type="primary">返回上一级</el-button>
        </el-col>
    </el-row><br>
    <el-row :gutter="20" class="bgep">
        <br>
        <el-col :span="8" :offset="4">
            <div>
                <el-form :model="menuForm" status-icon :rules="rules" ref="menuForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="菜单类型" prop="menuContents">
                        <el-radio-group v-model="menuForm.menuContents">
                            <el-radio label="1">目录</el-radio>
                            <el-radio label="2">菜单</el-radio>
                            <el-radio label="3">功能</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="上级菜单" prop="parentMenu">
                        <el-select v-model="menuForm.parentMenu" placeholder="请选择上级菜单">
                            <el-option v-if="menuForm.menuContents == 1" label="无上级菜单" value=""></el-option>
                            <el-option v-if="menuForm.menuContents == 2" label="系统管理" value="1"></el-option>
                            <el-option v-if="menuForm.menuContents == 3" label="用户管理" value="2"></el-option>
                            <el-option v-if="menuForm.menuContents == 3" label="角色管理" value="3"></el-option>
                            <el-option v-if="menuForm.menuContents == 3" label="菜单管理" value="4"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="菜单名称" prop="menuName">
                        <el-input v-model="menuForm.menuName"></el-input>
                    </el-form-item>
                    <el-form-item label="菜单标识" prop="menuIdentification">
                        <el-input v-model="menuForm.menuIdentification"></el-input>
                    </el-form-item>
                    <el-form-item label="菜单地址" prop="menuUrl">
                        <el-input v-model="menuForm.menuUrl"></el-input>
                    </el-form-item>
                    <el-form-item label="图标" prop="menuPhoto">
                        <el-input v-model="menuForm.menuPhoto"></el-input>
                    </el-form-item>
                    <el-form-item label="排序号" prop="sortNum">
                        <el-input v-model="menuForm.sortNum"></el-input>
                    </el-form-item>
                    <el-form-item label="状态" prop="menuStatus">
                        <el-radio-group v-model="menuForm.menuStatus">
                            <el-radio label="1">有效</el-radio>
                            <el-radio label="2">无效</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="备注" prop="menuRemarks">
                        <el-input type="textarea" v-model="menuForm.menuRemarks"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('menuForm')">立即创建</el-button>
                        <el-button @click="resetForm('menuForm')">重置</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
 data() {
      return {
        menuForm: {
          id:'',
          menuContents: '',
          parentMenu: '',
          menuName: '',
          menuIdentification:'',
          menuUrl: '',
          menuPhoto: '',
          sortNum: '',
          menuStatus: '',
          menuRemarks: ''
        },
        rules: {
          menuContents: [
            { required: true, message: '请选择菜单类型', trigger: 'change' }
          ],
          parentMenu: [
            { required: true, message: '请选择上级菜单', trigger: 'blur' },
          ],
          menuName: [
            { required: true, message: '请输入菜单名称', trigger: 'blur' },
            { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
          ],
          menuIdentification: [
            { required: false, message: '', trigger: 'blur' }
          ],
          menuUrl: [
            { required: false, message: '', trigger: 'blur' }
          ],
          menuPhoto: [
            { required: false, message: '', trigger: 'blur' }
          ],
          sortNum: [
            { required: true, message: '请输入排序号', trigger: 'blur' },
          ],
          menuStatus: [
            { required: true, message: '请选择状态', trigger: 'blur' },
          ],
          menuRemarks: [
            { required: false, message: '', trigger: 'blur' }
          ],
        }
      };
    },
     mounted(){
        var storage = localStorage;
        var menu =JSON.parse(storage.getItem('updateMenu'));
        menu.menuContents = menu.menuContents + '';
        menu.parentMenu = menu.parentMenu + '';
        menu.menuStatus = menu.menuStatus + '';
        if(menu.menuRemarks == null){
            menu.menuRemarks = '';
        }
        this.menuForm = menu;
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.menuForm);
            var that = this;
            var url="/updateMenu";
            let param = new URLSearchParams();
            param.set("id",that.menuForm.id);
            param.set("menuContents",that.menuForm.menuContents);
            param.set("parentMenu",that.menuForm.parentMenu);
            param.set("menuName",that.menuForm.menuName);
            param.set("menuIdentification",that.menuForm.menuIdentification);
            param.set("menuUrl",that.menuForm.menuUrl);
            param.set("menuPhoto",that.menuForm.menuPhoto);
            param.set("sortNum",that.menuForm.sortNum);
            param.set("menuStatus",that.menuForm.menuStatus);
            param.set("menuRemarks",that.menuForm.menuRemarks);

            that.$axios
              .post(url,param)
              .then(function(resp) {
               if(resp.data.success){
                 alert('修改成功!!');
                that.$router.push('/home/list');
               }
            })
              .catch(function (error) { // 请求失败处理
                  console.log(error);
            });
          } else {
            alert('修改失败!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      goBack(){
        var that = this;
        that.$router.push('/home/list');
      },
    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .bgep{
        background-color: white;
        color:black;
    }
</style>
