<template>
  <div>
    <div>
      <div align="left"><h2 style="color:	Gray">用户管理</h2></div>
      <el-row :gutter="20">
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-input
            size="medium"
            placeholder="请输入用户名"
            suffix-icon="el-icon-edit el-input__icon"
            v-model="user.username">
          </el-input>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-input
            size="medium"
            placeholder="请输入姓名"
            suffix-icon="el-icon-edit el-input__icon"
            v-model="user.name">
          </el-input>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-select size="medium" v-model="user.userStatus" placeholder="请选择状态">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-date-picker
            size="medium"
            value-format="yyyyMMdd"
            v-model="datavalue"
            type="daterange"
            start-placeholder="(创建)开始日期"
            end-placeholder="结束日期"
            :default-time="['00:00:00', '23:59:59']">
          </el-date-picker>
        </el-col>
        <el-col :span="8">
          <el-button v-if="btcheck.searchButton"
          type="primary"
          @click="search()">查询</el-button>
          <el-button v-if="btcheck.addButton"
          @click="addUser()">新增</el-button>
          <el-button
          type="danger" v-if="btcheck.delButton"
          @click="deleteMore()">删除</el-button>
        </el-col> 
      </el-row>
    </div><br>
  <el-table
    ref="multipleTable"
    :data="tableData"
    stripe
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    
    <el-table-column
      prop="id"
      label="序号">
    </el-table-column>
    <el-table-column
      prop="username"
      label="用户名"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="sex"
      label="性别"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="userPhone"
      label="联系电话"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="email"
      label="邮箱"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      label="创建时间"
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ timeFormat(scope.row.createTime) }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="最后修改时间"
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ timeFormat(scope.row.updateTime) }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="状态"
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.userStatus===1?'有效':'无效' }}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作" width="300" fixed="right">
      <template slot-scope="scope">
        <el-button
          size="mini"
          v-if="btcheck.updateButton"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          v-if="btcheck.delButton"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
         <el-button
          size="mini"
          type="warning"
          v-if="btcheck.updateStatus"
          @click="updateStatusDialog(scope.$index, scope.row)">{{{1: '有效', 2:'无效'}[scope.row.userStatus]}}</el-button>
        <el-button
          size="mini"
          v-if="btcheck.setRoleButton"
          @click="setRole(scope.$index, scope.row)">设置角色</el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="block">
    <br>
    <el-pagination
      @current-change="handleCurrentChange"
      :page-size="pageSize"
      background
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
  </div>
  <!--删除的弹窗 -->
    <el-dialog
      title="提示"
      :visible.sync="delDialog"
      width="30%"
      center>
      <span>是否删除序号{{delData}}的数据？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delDialog = false">取 消</el-button>
        <el-button type="primary" @click="deleteUser()">确 定</el-button>
      </span>
    </el-dialog>
   <!--修改状态的弹窗 -->
    <el-dialog
      title="提示"
      :visible.sync="statusDialog"
      width="30%"
      center>
      <span>是否修改状态？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="statusDialog = false">取 消</el-button>
        <el-button type="primary" @click="updateStatusTo()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data(){
    return{
      tableData:[],
      multipleSelection: [],
      total:0,
      user:{
        username:'',
        name:'',
        userStatus:'',
      },
      startTime:'',
      overTime:'',
      pageSize:10,//分页数目
      options: [
        {
          value: '',
          label: '请选择状态'
        },
        {
          value: '1',
          label: '有效'
        }, {
          value: '2',
          label: '无效'
        }],
        datavalue:'',//开始-结束时间
        btcheck:{//权限列
           searchButton:false,//查询
           addButton:false,//新增
           delButton:false,//删除
           updateButton:false,//编辑
           updateStatus:false,//修改状态
           setRoleButton:false,//设置角色
        },
        delDialog:false,//删除弹窗
        delData:[],//需删除的对象
        statusDialog:false,//修改状态弹窗
        updateStatus:{id:'',userStatus:''},//修改状态的对象
    }
  },
  mounted(){
         this.searchdata(1);
         this.updatePowers();
  },
  methods: {
    //跳转设置角色
    setRole(index, row){
      var that = this;
      var storage = localStorage;
      storage.setItem('setRoleUser',JSON.stringify(row));
      that.$router.push('/home/setRole');
    },
    //修改状态弹窗
    updateStatusDialog(index, row){
      var that = this;
      that.statusDialog = true;  
      that.updateStatus.id = row.id;
      if(row.userStatus == 1){
        that.updateStatus.userStatus = 2;
      };
      if(row.userStatus == 2){
        that.updateStatus.userStatus = 1;
      }
    },
    //修改状态
    updateStatusTo(){
      var that = this;
      var url="/updateUser";
      let param = new URLSearchParams();
      param.set("id",that.updateStatus.id);
      param.set("userStatus",that.updateStatus.userStatus);
       that.$axios
              .post(url,param)
              .then(function(resp) {
               if(resp.data.success){
                that.statusDialog = false; 
                that.searchdata(1);
               }
            })
              .catch(function (error) { // 请求失败处理
                  console.log(error);
            });
    },
    //跳转添加用户
    addUser(){
      var that = this;
      that.$router.push('/home/addUser');
    },
    //更新权限
    updatePowers(){
      var that = this;
      var storage = localStorage;
      var powers = JSON.parse(storage.getItem('powers'));
      console.log(powers);
      for(var j=0,len=powers.length;j<len;j++){
        if(powers[j].id == 5){
          that.btcheck.searchButton = true;
        }
        if(powers[j].id == 6){
          that.btcheck.addButton = true;
        }
        if(powers[j].id == 7){
          that.btcheck.delButton = true;
        }
        if(powers[j].id == 8){
          that.btcheck.updateButton = true;
        }
        if(powers[j].id == 17){
          that.btcheck.updateStatus = true;
        }
        if(powers[j].id == 18){
          that.btcheck.setRoleButton = true;
        }
      }
      console.log(powers);
    },
    //查询数据
    search(){
      var that = this;
      if(that.datavalue[0]!=undefined){
        that.startTime=that.datavalue[0]+"000000";
        that.overTime=that.datavalue[1]+"235959";
      }
      console.log(that.startTime,that.overTime);
      this.searchdata(1);
    },
    //分页
    searchdata(pageNumber){
         var that = this;
          var url="/queryUserPage";
          let param = new URLSearchParams();
          param.append("pageSize",that.pageSize);
          param.append("pageNumber",pageNumber);
          param.append("username",that.user.username);
          param.append("name",that.user.name);
          param.append("userStatus",that.user.userStatus);
          param.append("startTime",that.startTime);
          param.append("overTime",that.overTime);
          that.$axios
            .post(url,param)
            .then(function(resp) {
              that.tableData = resp.data.data;
              that.total = resp.data.total;
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
          });
    },
      //多选
      handleSelectionChange(val) {
        this.multipleSelection = val;
        
      },
      //编辑
      handleEdit(index, row) {
        console.log(index, row);
        var that = this;
        var storage = localStorage;
        storage.setItem('updateUser',JSON.stringify(row));
        that.$router.push('/home/updateUser');
      },
      //删除弹出
      handleDelete(index, row) {
        this.delData=[];
        this.delDialog = true;
        this.delData.push(row.id);
      },
      //批量删除
      deleteMore(){
        this.delDialog = true;
        this.delData=[];
        console.log(this.multipleSelection);
        for(var j=0 ,len=this.multipleSelection.length;j<len;j++){
           this.delData.push(this.multipleSelection[j].id);
        }
         console.log("多选:"+this.delData);
      },
      //删除
      deleteUser(){
        var that = this;
        var data = that.delData;
          var url="/deleteUser";
          let param = new URLSearchParams();
          param.append("uid",that.delData);
          that.$axios
            .post(url,param)
            .then(function(resp) {
             if(resp.data.success){
               that.delDialog = false;
               that.searchdata(1);
             }
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
          });
      },
     //跳转页数
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.searchdata(val);
      },
    //时间格式转换
      timeFormat (time){
           if (!time) {
              return '';
          }
          return time.replace(/(\d{4})(\d{2})(\d{2})(\d{2})(\d{2})(\d{2})/g, "$1-$2-$3 $4:$5:$6");
      },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
