<template>
  <div>
    <el-row :gutter="20">
        <el-col :span="2">
            <br>
            <el-button @click="goBack()" type="primary">返回上一级</el-button>
        </el-col>
    </el-row><br>

    <el-row :gutter="20">
        <el-col :span="24">
            <el-row>
                <el-col :span="2"><span style="color:red">*</span><span>用户名:</span></el-col>
                <el-col :span="22">
                    <el-input v-model="user.username" :disabled="true"></el-input>
                </el-col>
            </el-row>
        </el-col>
    </el-row><br>
    <el-row :gutter="20">
        <el-col :span="24">
            <el-row>
                <el-col :span="2"><span style="color:red">*</span><span>设置角色:</span></el-col>
                <el-col :span="22">
                     <el-table
                        ref="multipleTable"
                        :data="tableData"
                        stripe
                        tooltip-effect="dark"
                        style="width: 100%">
                      
                        <el-table-column label="选择" width="65">
                            <template scope="scope">
                                <el-radio :label="scope.row.id" 
                                v-model="user.roleId" 
                                :checked="user.roleId == scope.row.id ?true:false"
                                @change.native="getCurrentRow(scope.row)">
                                </el-radio>
                            </template>
                        </el-table-column>
                       
                        <el-table-column
                            prop="roleCode"
                            label="角色编码"
                            show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                            prop="roleName"
                            label="角色名称"
                            show-overflow-tooltip>
                        </el-table-column>
                        
                        <el-table-column
                            label="生效日期"
                            width="180">
                            <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ timeFormat(scope.row.startTime) }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                            label="失效日期"
                            width="180">
                            <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ timeFormat(scope.row.overTime) }}</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                            label="状态"
                            width="180">
                            <template slot-scope="scope">
                            <span style="margin-left: 10px">{{ scope.row.roleStatus===1?'有效':'无效' }}</span>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
        </el-col>
    </el-row>

    <br><br>
    <el-row :gutter="20">
        <el-col :span="24" center>
            <el-button @click="updateUser()" type="primary">提交</el-button>

            <el-button @click="user.roleId=''">清空</el-button>
        </el-col>
    </el-row><br>
    
  </div>
</template>

<script>
export default {
 data(){
   return{
     tableData:[],
     multipleSelection: [],
     options: [
        {
          value: '',
          label: '请选择状态'
        },
        {
          value: '1',
          label: '有效'
        }, {
          value: '2',
          label: '无效'
        }],
     user: {
          id:'',
          username: '',
          name: '',
          password: '',
          checkPassword:'',
          sex: '',
          userPhone: '',
          email: '',
          userStatus: '',
          userRemarks: '',
          roleId:'',
        },
   }
 },
  mounted(){
      var that = this;
      var storage = localStorage;
      that.user = JSON.parse(storage.getItem('setRoleUser'));
      that.user.roleId = that.user.roleId;
      console.log(that.user);
      this.searchdata();
  },
   methods: {
    //修改用户角色
    updateUser(){
          var that = this;
          if(that.user.roleId==undefined){
              alert("请选择一个角色!!!");
              return;
          }
          var url="/updateUser";
          let param = new URLSearchParams();
          param.set("id",that.user.id);
          param.set("roleId",that.user.roleId);
          that.$axios
          .post(url,param)
          .then(function(resp) {
               if(resp.data.success){
                 alert('修改成功!!');
                that.$router.push('/home/user');
               }
           })
            .catch(function (error) { // 请求失败处理
            console.log(error);
           });
    },
    getCurrentRow(row){ 
        //获取选中数据
        this.user.RoleId = row.id;
        console.log(this.user.RoleId);
    },
    //分页
    searchdata(){
         var that = this;
          var url="/queryAllRole";
          that.$axios
            .get(url)
            .then(function(resp) {
              that.tableData = resp.data.data;
          })
            .catch(function (error) { // 请求失败处理
                console.log(error);
          });
    },
    //时间格式转换
      timeFormat (time){
           if (!time) {
              return '';
          }
          return time.replace(/(\d{4})(\d{2})(\d{2})(\d{2})(\d{2})(\d{2})/g, "$1-$2-$3 $4:$5:$6");
      },
    //跳转
    goBack(){
        var that = this;
        that.$router.push('/home/user');
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
     .bgep{
        background-color: white;
        color:black;
    }
</style>
